/**
  * Handpicked Bootstrap 4 utilities
  */

@import './spacing';

.text-left {
    text-align: left !important;
}

.text-right {
    text-align: right !important;
}

.text-center {
    text-align: center !important;
}

.text-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.text-uppercase {
    text-transform: uppercase;
}

.p-0 {
    padding: 0;
}

.m-0 {
    margin: 0;
}

.mb-0 {
    margin-bottom: 0;
}

.img-fluid {
    max-width: 100%;
    height: auto;
}

.no-gutters {
    margin-right: 0;
    margin-left: 0;

    > .col,
    > [class*='col-'] {
        padding-right: 0;
        padding-left: 0;
    }
}

.w-100 {
    width: 100%;
}

.h-100 {
    height: 100%;
}

.no-user-select {
    user-select: none;
}

.small {
    font-size: 80%;
}
