@import 'node_modules/bootstrap/dist/css/bootstrap-reboot.min';
@import 'node_modules/bootstrap/dist/css/bootstrap-grid.min';
@import '_shared_/sass/bootstrap_handpicked';
@import '_shared_/sass/theme_ui';
@import 'variables';
@import '_shared_/sass/icons';
@import '_shared_/sass/tooltips';

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
        'Helvetica Neue', sans-serif;
    letter-spacing: 0;
    font-weight: 400;
    font-style: normal;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -moz-font-feature-settings: 'liga' on;
    color: rgba(0, 0, 0, 0.84);
    font-size: 16px;
    line-height: 1.4;
}

html,
body {
    height: 100%;
    width: 100%;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    overscroll-behavior-y: contain;
    background-color: transparent;
    touch-action: manipulation;
    overflow: hidden;
    transition: all 300ms ease-in;
}

html.ready,
html.ready body {
    background-color: var(--theme-background-color);
    opacity: 1;
}

button {
    position: relative;
    display: inline-block;
    margin: 0;
    padding: 0;
}

@media print {
    * {
        display: none !important;
    }
}
