.actionBtn {
    background-color: transparent;
    color: var(--theme-text-color);
    border: none;
    cursor: pointer;
    position: relative;
    &:focus {
        outline: none;
    }
    &:hover:before {
        opacity: 0.8;
    }
}

.flatButton {
    background-color: transparent;
    color: var(--theme-icons-color);
    border: none;
    position: relative;
    opacity: 0.9;
    cursor: pointer;
    transition: all 150ms ease;
    > div,
    svg {
        position: relative;
        z-index: $z3;
    }
    div:before {
        content: '';
        position: absolute;
        background-color: var(--theme-icons-background);
        border-radius: 50%;
        width: 105%;
        height: 105%;
        bottom: 0;
        left: -1px;
        transform: scale(1.2);
        transform-origin: center center;
        opacity: 0;
        z-index: $z2;
        transition: opacity 0.2s ease-in-out, scale 150ms ease-in;
    }
    &:focus {
        outline: none;
    }
    &:active div:before,
    &:focus div:before {
        opacity: 0.8;
        transform: scale(1.1);
        //add animation then
    }
    &:hover {
        opacity: 1;
        div:before {
            opacity: 0.3;
        }
    }
    &-unActive {
        div::before {
            visibility: hidden;
            display: none;
        }
    }
    &.active {
        div:before {
            opacity: 0.6;
            transform: scale(1.2);
        }
    }
}

.linearButton {
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    border: 1px solid var(--theme-button-border);
    color: var(--theme-title-color);
    background-color: var(--theme-button-background);
    border-radius: 3px;
    padding: 0.2rem 1rem;
    @extend .shadow !optional;
    &:hover {
        @extend .shadow-lg !optional;
    }
    &:disabled {
        box-shadow: none;
        opacity: 0.5;
        cursor: not-allowed;
    }
}

.secondaryButton {
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    border: 1px solid transparent;
    color: var(--theme-title-color);
    background-color: var(--theme-button-background);
    border-radius: 3px;
    padding: 0.2rem 1rem;
    @extend .shadow !optional;
    &:hover {
        @extend .shadow-lg !optional;
    }
    &:disabled {
        box-shadow: none;
        opacity: 0.5;
        cursor: not-allowed;
    }
}

.secondaryButton.danger {
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    border-color: red;
    color: red;
    background-color: var(--theme-button-background);
    &:hover {
    }
}

.flatButton--buyNow {
    background: var(--theme-button-buynow-background);
    color: var(--theme-button-buynow-color);
    padding: 0.3rem 1rem;
    border-radius: 3px;
}
